@charset "utf-8";

*{margin:0px;padding:0px;}
ul, ol, li{list-style:none;}
body{
  font-family: "맑은 고딕",arial, sans-serif;
  font-size:12px;
  color:#333;
  margin: 3%;
}
main, header{text-align: center;}
header img{width: 180px;}
main h2{font-size: 20px; margin: 10px 0;}
.inputData{
  text-align: left;
  width: 100%;
}
.inputData p{margin-bottom: 12px; font-size: 16px;}
.inputData p label {display: inline-block; width: 150px;}
.inputData p input{padding: 10px; width: calc(100% - 180px);}
.inputData p:last-child{text-align: center;}
.inputData p:last-child input{width: 100%; border: none; background: #009c75; color: #fff; cursor: pointer;}
.inputData p:last-child input:hover{background: #004b38;}

.proUl{text-align: left;}
.proUl li {font-size: 16px; line-height: 20px;}
.proUl li p{margin-bottom: 10px;}
.Price{font-size: 18px; font-weight: bold;}
.cBtn{text-align: center;}
.cBtn input{width: 100%; border: none; background: #ff661f; color: #fff; cursor: pointer; height: 30px;}
.cBtn input:hover{background: #e94a00;}